:root {
  --bg-primary: white;
  --text-primary: #333;
  --text-secondary: #555;
  --border-color: #eee;
  --accent-color: #007bff;
  --hover-bg: #f8f9fa;
  --shadow-color: rgba(0, 0, 0, 0.4);
  --error-bg: #fee2e2;
  --error-border: #ef4444;
  --error-text: #dc2626;
  --btn-secondary-bg: #6c757d;
  --btn-primary-bg: var(--interface-color);
  --btn-primary-color: var(--interface-icon-color);
  --interface-color: #348de6;
  --interface-icon-color: #ffffff;
}

.color-picker-group {
  margin-bottom: 20px;
}

.color-picker-label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #333;
}

.color-picker-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.color-picker-wrapper .react-colorful {
  width: 100%;
  max-width: 200px;
  height: 150px;
}

.color-input {
  width: 100px;
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-family: monospace;
  font-size: 14px;
  text-transform: uppercase;
}

.color-input:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}


.settings-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1001;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.25rem;
  transition: opacity 0.2s ease;
}

.settings-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.95);
  width: 100vw;
  max-width: 35.25rem;
  max-height: 90vh;
  background: var(--bg-primary);
  border-radius: 1rem;
  box-shadow: 0 1.25rem 3.75rem var(--shadow-color);
  z-index: 1002;
  overflow: auto;
  animation: modalEnter 0.3s ease-out forwards;
}

@keyframes modalEnter {
  to {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem 1rem;
  border-bottom: 1px solid var(--border-color);
}

.settings-title {
  margin: 0;
  font-size: 1.8em;
  color: var(--text-primary);
}

.settings-close-btn {
  background: var(--hover-bg);
  border: 2px solid var(--border-color);
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  cursor: pointer;
  font-size: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: var(--text-secondary);
}

.settings-close-btn:hover {
  background: var(--border-color);
  color: var(--text-primary);
}

.settings-close-btn:focus-visible {
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
}

.settings-content {
  padding: 0 2rem 1.5rem;
}

.error-toast {
  position: relative;
  background: var(--error-bg);
  color: var(--error-text);
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  border-left: 0.25rem solid var(--error-border);
  margin: 1rem 0;
  box-shadow: 0 0.25rem 0.75rem rgba(239, 68, 68, 0.2);
  animation: slideDown 0.3s ease-out;
  font-weight: 500;
}

@keyframes slideDown {
  from {
    transform: translateY(-1.25rem);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.settings-section-title {
  margin: 1.5rem 0 1rem 0;
  color: var(--text-primary);
  font-size: 1.3em;
  font-weight: 600;
}

.settings-item {
  display: flex;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid var(--border-color);
}

.settings-label {
  flex: 1;
  font-weight: 500;
  color: var(--text-secondary);
}

.settings-select {
  padding: 0.5rem 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: 0.375rem;
  background: var(--bg-primary);
  font-size: 0.875rem;
  min-width: 5rem;
}

.settings-select:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 0.1875rem rgba(0, 123, 255, 0.1);
}

.operations-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}

.operations-group {
  margin-bottom: 1rem;
}

.operations-main-label {
  padding: 0.5rem 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: 500;
  border-radius: 0.375rem;
  transition: background 0.2s;
}

.operations-main-label:hover {
  background: var(--hover-bg);
}

.operations-main-checkbox {
  width: 1.125rem;
  height: 1.125rem;
  accent-color: var(--interface-color);
  margin-right: 0.625rem;
}

.operations-main-checkbox:focus-visible {
  outline: 2px solid var(--interface-color);
  outline-offset: 2px;
}

.trig-subgrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
  margin-left: 1.5rem;
  margin-top: 0.5rem;
  padding: 0.75rem;
  background: var(--hover-bg);
  border-radius: 0.5rem;
}

.trig-subgrid label {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  cursor: pointer;
  font-size: 0.95em;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  transition: background 0.2s;
}

.trig-subgrid label:hover {
  background: var(--bg-primary);
}

.theme-toggle {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  cursor: pointer;
  font-weight: 500;
  padding: 0.75rem 0;
  border-radius: 0.375rem;
  transition: background 0.2s;
}

.theme-toggle:hover {
  background: var(--hover-bg);
}

.theme-checkbox {
  width: 1.125rem;
  height: 1.125rem;
  accent-color: var(--accent-color);
}

.theme-checkbox:focus-visible {
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
}

.settings-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: flex-end;
  padding: 1.5rem 2rem;
  border-top: 1px solid var(--border-color);
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.375rem;
  cursor: pointer;
  font-weight: 500;
  font-size: 0.875rem;
  transition: all 0.2s;
}

.btn-secondary {
  background: var(--btn-secondary-bg);
  color: white;
}

.btn-secondary:hover {
  transform: translateY(-1px);
}

.btn-primary {
  background: var(--btn-primary-bg);
  color: var(--interface-icon-color);
}

.btn-primary:hover {
  transform: translateY(-1px);
}

.btn-disabled,
.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  pointer-events: none;
}

.btn:focus-visible {
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
}

@media (max-width: 37.5rem) {
  .settings-overlay {
    padding: 0.625rem;
  }

  .settings-modal {
    width: 95vw;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 0;
    max-height: 100vh;
    transform: none;
    animation: none;
  }

  .settings-header,
  .settings-content,
  .settings-actions {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }

  .operations-grid {
    grid-template-columns: 1fr;
  }

  .trig-subgrid {
    grid-template-columns: 1fr 1fr;
  }
}